    <img v-bind:src="indicator.icon" alt="indicator-icon">
    <div class="v-indicator-info">
      <p class="v-indicator-number">{{ indicator.number }}</p>
      <p class="v-indicator-name">{{ indicator.name }}</p>
    </div>
  </div>
</template>

<script>
export default {
  name: 'v-indicator',
  props: {
    indicator: Object,
  }
}
</script>

