  .v-indicator {
    display: flex;
    align-items: center;
  }

  .v-indicator img {
    margin: 0 15px 0 0;
  }

  .v-indicator-number {
    font-size: 16px;
    line-height: 20px;
    font-weight: 500;
    color: #333333;
  }

  .v-indicator-number::after {
    content: '';
    display: block;
    width: 31px;
    height: 1px;
    background-color: #E0E0E0;
    margin: 2px 0 0;
  }

  .v-indicator-name {
    font-family: 'Open Sans', sans-serif;
