<template lang="html">
  <div class="v-blog-post">
    <img v-bind:src="post.img" alt="blog-post-img">
    <div class="v-post-content" v-if="post.active">
      <h3>{{ post.title }}</h3>
      <p>{{ post.description }}</p>
    </div>
  </div>
</template>

<script>
export default {
  name: 'v-blog-post',
  props: {
    post: Object,
  }
}
</script>

<style lang="css" scoped>
  .v-post-content {
    padding: 20px 20px 30px;
    border: 1px solid #F2F2F2;
  }

  .v-post-content p {
    margin: 15px 0 0;
    line-height: 20px;
    font-weight: 300;
  }
</style>
