    <h6 class="v-wine-category-name">{{ category.name }}</h6>
    <p class="v-wine-category-sum small-text">{{ category.sum + 'positions' }}</p>
  </div>
</template>

<script>
export default {
  name: 'v-wine-category',
  props: {
    category: Object,
  }
}
</script>

